<script>
export default {
  name: "Goods",
  data() {
    return {
      good: null,
      num: 1,
      goodType: [],
      v: 0,
      version: "",
    };
  },
  watch: {
    good: {
      deep: true,
      handler(newval) {
        if (this.good !== null) {
          localStorage.setItem("goods", JSON.stringify(newval));
        }
      },
    },
  },
  methods: {
    pushShopcart() {
      if (localStorage.getItem("username")) {
        this.$router.push({
          name: "gouwuche",
          params: {
            good: {
              id: this.good.id,
              title: this.good.title,
              price: this.good.price,
              version: this.version,
              num: this.num,
              url: this.good.picture[2],
              done:0
            },
          },
        });
      } else {
        alert("请先登录");
        this.$router.push({
          name: "denglu",
        });
      }
    },
    jia() {
      this.num++;
    },
    jian() {
      if (this.num > 1) {
        this.num--;
      }
    },
    getGood() {
      return this.$route.params.good;
    },
    getPicture(index) {
      let reg = /img\/(.*).jpg$/;
      let res = [];
      this.good.picture.forEach((el) => {
        let str = el.src;
        res.push(str.match(reg)[1]);
      });
      return require("../assets/img/" + res[index] + ".jpg");
    },
    isShow() {
      return this.goodType.length > 0 ? true : false;
    },
    getDetail() {
      let reg = /img\/(.*).jpg$/;
      let res = [];
      this.good.details.forEach((el) => {
        let str = el.src;
        res.push(str.match(reg)[1]);
      });
      return require("../assets/img/" + res + ".jpg");
    },
  },
  created() {
    // console.log("创建前", this.good);
    if (!localStorage.getItem("goods")) {
      this.good = this.getGood();
      // console.log("进入没有本地存储", this.good);
    } else {
      //判断是不是从路由跳转的
      if (!this.getGood()) {
        this.good = JSON.parse(localStorage.getItem("goods"));
      } else {
        //判断本地存储是否为同一个
        let id1 = this.getGood().id;
        let id2 = JSON.parse(localStorage.getItem("goods")).id;
        this.good =
          id1 == id2
            ? JSON.parse(localStorage.getItem("goods"))
            : this.getGood();
        // console.log("进入有本地存储", this.good);
      }
    }
    this.goodType = this.good.type;
    // console.log("从good获取的type", this.goodType);
    if (this.goodType) {
      this.goodType = JSON.parse(this.goodType);
      // console.log("type有内容，转数组", this.goodType);
    }
  },
  mounted() {
    function getPagePoint(e) {
      e = e || window.event; //事件对象兼容
      return {
        pageX: e.pageX || getPageScroll().scrollLeft + e.clientX,
        pageY: e.pageY || getPageScroll().scrollTop + e.clientY,
      };
    }
    /* 给导航栏的地址区域添加点击改变事件*/
    var aDdres = document.querySelectorAll(".navgation .item a");
    var aDdsho = document.querySelectorAll(".navgation .add span")[0];
    for (var i = 0; i < aDdres.length; i++) {
      aDdres[i].onclick = function() {
        for (var j = 0; j < aDdres.length; j++) {
          aDdres[j].className = "";
        }
        aDdsho.innerText = this.innerText;
        this.className = "seaon";
      };
    }
    //获取所有的颜色款式
    var steCol = document.querySelectorAll(".stecol ul>li");
    //获取所有大小样式
    var steSize = document.querySelectorAll(".stesize ul>li");
    //获取增值样式
    var zengZhi = document.querySelectorAll(".zengzhi .li");
    // 获取分期样式
    var fenQi = document.querySelectorAll(".fenqi .li");
    var baiTiao = document.querySelector(".baitiao");
    //给所有的颜色款式绑定点击事件
    for (var i = 0; i < steCol.length; i++) {
      steCol[i].onclick = function() {
        //排他思想
        for (var j = 0; j < steCol.length; j++) {
          if (steCol[j] == this) {
            this.className = "current";
          } else {
            steCol[j].className = "";
          }
        }
      };
    }
    //给尺寸大小添加点击事件
    for (var i = 0; i < steSize.length; i++) {
      steSize[i].onclick = function() {
        for (var j = 0; j < steSize.length; j++) {
          if (steSize[j] == this) {
            this.className = "current";
          } else {
            steSize[j].className = "";
          }
        }
      };
    }
    //给增值样式绑定点击事件
    for (var i = 0; i < zengZhi.length; i++) {
      zengZhi[i].onclick = function() {
        if (this.className == "li current") {
          this.className = "li";
        } else {
          this.className = "li current";
        }
      };
    }
    //给分期样式绑定点击事件
    for (var i = 0; i < fenQi.length; i++) {
      fenQi[i].onclick = function() {
        for (var j = 0; j < fenQi.length; j++) {
          if (fenQi[j] == this) {
            this.className = "current";
          } else {
            fenQi[j].className = "";
          }
        }
        if (this.id != "nofq") {
          baiTiao.style.display = "block";
        } else {
          baiTiao.style.display = "none";
        }
      };
    }
    var bigImg = document.querySelector(".gs_bigbox img");
    /* 商品图片放大展示效果 */
    var gsBigbox = document.querySelector(".gs_bigbox");
    var mask = document.querySelector(".mask");
    var gslImgbox = document.querySelector(".gsl_img");
    var gscontent = document.querySelector("#gscontent");
    gslImgbox.onmouseover = function() {
      gsBigbox.style.display = "block";
      mask.style.display = "block";
    };
    gslImgbox.onmouseout = function() {
      gsBigbox.style.display = "none";
      mask.style.display = "none";
    };
    //绑定鼠标移动事件
    gslImgbox.onmousemove = function(e) {
      e = e || window.event;
      var w =
        getPagePoint().pageX - gscontent.offsetLeft - mask.offsetWidth / 2;
      var h =
        getPagePoint().pageY - gscontent.offsetTop - mask.offsetHeight / 2;

      //边界检测
      w = w < 0 ? 0 : w;
      w = w > 112 ? 112 : w;
      h = h < 0 ? 0 : h;
      h = h > 112 ? 112 : h;
      mask.style.left = w + "px";
      mask.style.top = h + "px";

      //大图显示对应位置
      bigImg.style.left =
        (-w * gslImgbox.offsetWidth) / mask.offsetWidth + "px";
      bigImg.style.top =
        (-h * gslImgbox.offsetHeight) / mask.offsetHeight + "px";
    };
  },
  beforeRouteLeave(to, from, next) {
    if (this.goodType.length > 0) {
      this.version = this.goodType[this.v].version;
    } else {
      this.version = "";
    }
    next();
  },
};
</script>

